import styles from "./TariffsItem.module.css";

const TariffsItem = (props) => {
    let state = props.el;
    return (
        <tr className={state.tariff_name.toLowerCase().includes(" -") ? styles.bold : ""}>
            <td>
                <div>{state.tariff_name}</div>
                {state.tariff_name.toLowerCase().includes("") ?
                    <div>(  ,   )</div> : null}
            </td>
            <td>{state.delivery_sum + " "}.</td>
            <td>{props.insuranceCost + " "}.</td>
            <td> {" " + state.period_min + " "}  {" " + state.period_max + " "} . </td>
            <td>{state.total_delivery_cost + " "} .</td>
        </tr>
    )
}

export default TariffsItem;